import members from './members.json';
import { Card } from '../components/'

const Members = () =>{
    return (
        <div>
            <h1> our members </h1>
            {
                members.members.map((value,index) => {
                    return (
                      <Card
                        key={index}
                        nickname={value.nickname}
                        contact={value.contact}
                        image={value.image}
                      />
                    );
                })
            }
        </div>
    )
}

export default Members