import '../stylesheets/Card.css';

const Card = ({key,nickname,contact,image}) => {
    const errorHandler = e => {
        e.target.src = "/images/default.gif"
    }
    return (
        <div className="CardContainer" key={key}>
            <img src={`/images/${image}`} onError={errorHandler} alt="Image_Error_ㅜㅜ" />
            <p>member {nickname}</p>
            <p>contact to {contact}</p>
        </div>
    )
}

export default Card